<template>
     <div class="p-grid">
        <p-data-table :value="historyData">
            <p-column field="patiendId" header="ID" />
            <p-column field="containerId" header="Container Id" />
            <p-column field="containerName" header="Container Name" />
            <p-column field="barcode" header="Barcode" />
            <p-column field="generatedAt" header="Generated At" />
            <p-column field="patientFirstName" header="Patient First Name" />
            <p-column field="patientLastName" header="Patient Last Name" />
            <p-column field="gender" header="gender" />
            <p-column field="savedBy" header="Saved By" />
        </p-data-table>                         
    </div>
</template>

<script>

import { ref, onMounted } from "vue";
import { toRefs } from "vue-demi";
import { $axios } from "@/service/app-http";

export default {
    props: {
        patientId: Number
    },
    setup(props) {
        const historyData = ref([]);
        const { patientId } = toRefs(props);

        const fetchData = async (patient_id) => {
       
           // try {
                const { data } = await $axios.get(`lab-barcode/list-scan-items/${patient_id}`, {
                    params: {}
                });
                                              
                historyData.value = data.data;                 

           // } catch (err) {}
        }

        // Call fetchData when the component is created
        onMounted(() => {
            fetchData(patientId.value);
        });

        return {
            historyData 
        };
    }
};
</script>

