<template>
    <div>
        <div
            style="display:flex; justify-content: space-between; background-color: #e8d8a7; padding: 10px; margin-bottom: 10px"
        >
            <div class="title-and-input" style="margin-bottom:5px;">
                <div>
                    <p-button
                        label="Back"
                        class="p-button-sm"
                        style="margin-bottom: 10px;"
                        @click="goToBarcodePage"
                    />
                </div>
                <label for="barcode-scanner">Please scan barcode here</label>
                <input
                    type="text"
                    class="p-inputtext"
                    v-model="scannedBarcode"
                    placeholder="Scan Barcode"
                    id="barcode-scanner"
                    @keyup.enter="handleBarcodeScan"
                    ref="inputRef"
                    v-on:blur="fopcusOut"
                    style="width:250px;"
                />
            </div>
            <div class="section">
                <div>
                    <div
                        style="display: inline-block;"
                        v-if="scan_person !== null"
                        :key="index"
                    >
                        <div
                            class="block-item1"
                            style="background-color:#FFFFFF;"
                        >
                            <div style="margin-right: 15px; ">
                                <img
                                    v-if="patientPicture"
                                    style="width: 100px; height: 100px;"
                                    :src="patientPicture"
                                />
                                <img
                                    v-else-if="scan_person.gender == 'F'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <img
                                    v-else
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                            </div>
                            <div
                                class="inner-block"
                                style="background-color:#d3D3D3;"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span
                                            >{{ scan_person.patientId }}
                                            {{ scan_person.patientFirstName }}
                                            {{
                                                scan_person.patientLastName
                                            }}</span
                                        >
                                        <span
                                            >{{ scan_person.patientDOB }}
                                            {{ scan_person.gender }}</span
                                        >
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                            <div
                                style="width: 100px; font-style: italic; display:flex; justify-items:center; align-items:center;"
                            >
                                <center>{{ scan_person.scannedAT }}</center>
                            </div>
                            <div
                                class="inner-block"
                                style="background-color:#d3D3D3;"
                                v-if="scan_person.partnerId !== null"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span
                                            >{{ scan_person.partnerId }}
                                            {{ scan_person.partnerFirstName }}
                                            {{
                                                scan_person.partnerLastName
                                            }}</span
                                        >
                                        <span
                                            >{{ scan_person.partnerDOB }}
                                            {{
                                                scan_person.partnerGender
                                            }}</span
                                        >
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                            <div style="margin-left: 15px; ">
                                <img
                                    v-if="partnerPicture"
                                    style="width: 100px; height: 100px;"
                                    :src="partnerPicture"
                                />
                                <img
                                    v-else-if="scan_person.partnerGender == 'F'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <img
                                    v-else
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/men.png"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div>
            <div class="section">
                <div>
                    <div
                        style="display: inline-block;"
                        v-for="(item, index) in scan_items"
                        :key="index"
                    >
                        <div
                            class="block-item"
                            :style="{
                                'background-color':
                                    item.type !== 'person'
                                        ? '#FC94AF'
                                        : '#FFFFFF'
                            }"
                        >
                            <div style="margin-right: 15px; ">
                                <p>{{ item.title }}</p>
                                <img
                                    v-if="item.type === 'person'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <p
                                    v-else
                                    style="width: 100px; height: 100px; ;"
                                />
                            </div>
                            <div
                                class="inner-block"
                                :style="{
                                    'background-color':
                                        item.type !== 'person'
                                            ? '#D4A373'
                                            : '#d3D3D3'
                                }"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span>{{ item.id }}</span>
                                        <span>{{ item.firstName }}</span>
                                        <span>{{ item.lastName }}</span>
                                        <span>{{ item.dob }}</span>
                                        <span>{{ item.gender }}</span>
                                        <span>{{ item.scannedAt }}</span>
                                    </div>
                                    <div>
                                        <p-button
                                            icon="pi pi-times"
                                            class="p-button-sm"
                                            @click="closeBlockItem(item, index)"
                                        />
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div style="display: flex; justify-content: space-between;">
            <div>
                <p-button
                    label="Reset"
                    class="p-button-lg p-button-danger"
                    @click="resetScanItems"
                />
            </div>
            <div>
                <p-dropdown
                    v-model="selectedOption"
                    id="selectedOption"
                    :options="actionOptions"
                    optionLabel="label"
                    optionValue="value"
                    :showClear="true"
                    style="min-width: 400px; background-color: #e8d8a7; min-height: 38px;"
                />
                <p-button
                    label="Save"
                    class="p-button-lg"
                    style="margin-left: 10px;"
                    @click="saveScanItems"
                />
            </div>
        </div>
        <p-dialog
            header="Scanned History"
            v-model:visible="historyDialogVisible"
            @onHide="closeHistoryDialog"
            style="width: 1000px;"
        >
            <patient-scan-history :patientId="personId" />
        </p-dialog>
    </div>
</template>
<script>
import { $axios } from "@/service/app-http";
import { onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { serialize } from "php-serialize";
import { ZEBRA_PRING_URL, NL_FORM_BASE } from "@/constants";
import { ORIGIN } from "./../../config";
import { useToast } from "primevue/usetoast";
import { Dialog } from "primevue/dialog";
import PatientScanHistory from "@/components/test/patient-scan-history.vue";
import { useRouter } from "vue-router";

export default {
    components: {
        PatientScanHistory
    },
    setup() {
        const store = useStore();
        const inputRef = ref(null);
        const loading = ref(false);
        const patientPicture = ref(null);
        const partnerPicture = ref(null);

        const scannedBarcode = ref(null);
        let focusing = false; // A flag to track if focusing is in progress
        const toast = useToast();
        const personId = ref(null);
        const selectedOption = ref(null);
        const patient = ref({});
        const partner = ref({});
        const historyDialogVisible = ref(false);
        const historyData = ref([]);
        const scan_items = ref([]);
        const scan_person = ref(null);
        const actionOptions = ref([
            {
                label: "ICSI",
                value: "ICSI"
            },
            {
                label: "stripping",
                value: "stripping"
            },
            {
                label: "Ca++",
                value: "Ca++"
            },
            {
                label: "IVF",
                value: "IVF"
            },
            {
                label: "transfer to ET dish",
                value: "transfer to ET dish"
            },
            {
                label: "fertilization check",
                value: "fertilization check"
            },
            {
                label: "day 3",
                value: "day 3"
            },
            {
                label: "day 5",
                value: "day 5"
            },
            {
                label: "OPU",
                value: "OPU"
            },
            {
                label: "ET",
                value: "ET"
            },
            {
                label: "embryo glue",
                value: "embryo glue"
            },
            {
                label: "vitrify",
                value: "vitrify"
            },
            {
                label: "insemination",
                value: "insemination"
            }
        ]);

        const showHistoryDialog = () => {
            historyDialogVisible.value = true;
        };

        const closeHistoryDialog = () => {
            historyDialogVisible.value = false;
        };
        // Function to add a scanned item to scan_items if it's not already present
        const addScannedItem = (item) => {
            const barcodeExists = scan_items.value.some(
                (existingItem) => existingItem.id === item.id
            );
            if (!barcodeExists) {
                scan_items.value.push(item);
            }
        };

        // Function to handle barcode scanning
        const handleBarcodeScan = () => {
            // Process barcode data and create the item object
            // Replace the following line with your barcode data processing logic
            processBarcodeData(scannedBarcode.value);
            scannedBarcode.value = "";
        };

        // Example function for processing barcode data
        const processBarcodeData = async (barcode) => {
            if (!isNaN(barcode)) {
                if (scan_person.value === null) {
                    try {
                        // get person data axsios
                        const { data } = await $axios.get(
                            `lab-barcode/get-person-data/${barcode}`,
                            {
                                params: {}
                            }
                        );

                        personId.value = data.patientId;
                        scan_person.value = data;

                        getProfilePictures(
                            scan_person.value.patientId,
                            scan_person.value.partnerId
                        );
                        // getProfilePictures(794531, 794531);
                    } catch (err) {}
                }
            } else {
                if (personId.value !== null) {
                    // get barcode data axios
                    const { data } = await $axios.get(
                        `lab-barcode/get-patient-barcode-data/${personId.value}/${barcode}`,
                        {
                            params: {}
                        }
                    );

                    const scannedItem = {
                        id: data.barcode,
                        type: "container",
                        title: data.containerName,
                        logo: "logo1",
                        firstName: data.patientFirstName,
                        lastName: data.patientLastName,
                        dob: data.patientDOB,
                        gender: data.sex,
                        scannedAt: data.scannedAT
                    };

                    addScannedItem(scannedItem);

                    // getProfilePictures(794531, 794531);
                } else {
                    // get barcode data axios
                    const { data } = await $axios.get(
                        `lab-barcode/get-barcode-data/${barcode}`,
                        {
                            params: {}
                        }
                    );

                    const ldata = data.labBarcode;

                    const scannedItem = {
                        id: ldata.barcode,
                        type: "container",
                        title: ldata.containerName,
                        logo: "logo1",
                        firstName: ldata.patientFirstName,
                        lastName: ldata.patientLastName,
                        dob: ldata.patientDOB,
                        gender: ldata.sex,
                        scannedAt: ldata.scannedAT
                    };

                    const person = data.labPatient;
                    personId.value = person.patientId;
                    scan_person.value = person;

                    addScannedItem(scannedItem);

                    getProfilePictures(
                        scan_person.value.patientId,
                        scan_person.value.partnerId
                    );
                }
            }
        };

        const getProfilePictures = async (patientId, partnerId) => {
            if (patientId) {
                try {
                    const { data } = await $axios.get(
                        `oscar/patient/pp?patientId=${patientId}`
                    );

                    patientPicture.value = data.photoBase64;
                    //console.log(data);
                } catch (err) {}
            }

            if (partnerId) {
                try {
                    const { data } = await $axios.get(
                        `oscar/patient/pp?patientId=${partnerId}`
                    );

                    partnerPicture.value = data.photoBase64;
                } catch (err) {}
            }
        };

        const closeBlockItem = (item, index) => {
            // Use splice to remove the item from scan_items by index

            if (item.type === "person") scan_items.value = [];
            else scan_items.value.splice(index, 1);
        };

        const saveScanItems = async () => {
            // console.log('Scan items data:', scan_items.value);

            if (selectedOption !== null || selectedOption !== "") {
                // console.log(selectedOption.value)

                const payload = {
                    action: selectedOption.value,
                    patientId: personId.value,
                    scannedItems: scan_items.value
                };

                try {
                    const { data } = await $axios.post(
                        `lab-barcode/save-scans`,
                        payload
                    );
                    console.log(data);

                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Scan items saved successfully",
                        life: 5000 // Adjust this value as needed
                    });
                } catch (err) {}
            }
        };

        const resetScanItems = () => {
            scan_items.value = [];
            scan_person.value = null;
            personId.value = null;
        };

        const router = useRouter();
        onMounted(() => {
            //console.log(store.state.artCycle.patient);
            focusInput();
        });

        // Watch for changes in the "scannedBarcode" variable to trigger refocusing
        watch(scannedBarcode, () => {
            if (!focusing) {
                // If the input loses focus and "scannedBarcode" changes, refocus it
                focusInput();
            }
        });

        // Function to focus the input field
        const focusInput = () => {
            focusing = true;
            if (inputRef.value) {
                inputRef.value.focus();
            }
            focusing = false;
        };

        const fopcusOut = () => {
            focusing = false;
            focusInput();
        };

        return {
            loading,
            patient,
            partner,
            partnerPicture,
            patientPicture,
            personId,
            store,
            scan_items,
            scannedBarcode,
            handleBarcodeScan,
            closeBlockItem,
            saveScanItems,
            selectedOption,
            toast,
            inputRef,
            fopcusOut,
            historyDialogVisible,
            historyData,
            showHistoryDialog,
            closeHistoryDialog,
            scan_person,
            actionOptions,
            resetScanItems,
            goToBarcodePage: () => {
                router.push({ path: "/barcodes" });
            },
           
        };
    }
};
</script>

<style scoped>
.p-card .p-card-content {
    padding: 0;
}

.block-item1 {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
}

.block-item {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.title-and-input {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-left: 10px;
    margin-top: 35px;
}

.inner-block {
    padding: 10px;
}

.separator {
    width: 1px;
    background-color: #000; /* Set the color of the separator */
    margin: 0 10px; /* Adjust the margin to control the spacing */
}
</style>
